<template>
  <div class="app-container">
    <!-- <router-link to="/oms/orderDelivery">
       <el-button type="primary" style="margin-bottom: 20px;">订单派单</el-button>
     </router-link>-->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float:right" type="primary" @click="handleSearchList()" size="small">查询搜索</el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small"
        >重置</el-button>
      </div>
      <div style="margin-top:25px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <!--<el-form-item label="输入搜索：">
            <el-input v-model="listQuery.KeyWords" class="input-width" placeholder="关键字"></el-input>
          </el-form-item>-->
          <el-form-item label="商品：">
            <el-input
              v-model="listQuery.KeyWords"
              class="input-width"
              placeholder="商品名称"
            ></el-input>
          </el-form-item>
          <el-form-item label="订单号：">
            <el-input
              v-model="listQuery.OrderNo"
              class="input-width"
              placeholder="订单编号"
            ></el-input>
          </el-form-item>
          <el-form-item label="订单状态：">
            <el-select v-model="listQuery.Status" class="input-width" placeholder="全部" clearable>
              <el-option
                v-for="item in statusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单类型：">
            <el-select v-model="listQuery.OrderType" class="input-width" placeholder="全部" clearable>
              <el-option
                v-for="item in OrderTypeList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="选择时间：">
            <el-date-picker
              v-model="listQuery.checkTime"
              type="daterange"
              align="right"
              unlink-panels
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <el-button class="btn-add" @click='print' size="mini">打印</el-button>
    </el-card>


    <div class="table-container">
      <el-table
        ref="orderTable"
        :data="list"
        style="width: 100%;"
        @selection-change="handleSelectionChange"
        v-loading="listLoading"
        border
          id="orderDeliveryID"
      >
        <!--<el-table-column type="selection" width="60" align="center"></el-table-column>-->
        <!-- <el-table-column label="订单ID" align="center">
          <template slot-scope="scope">{{scope.row.OrderHeadID}}</template>
        </el-table-column> -->
        <el-table-column label="序列号" align="center"  type="index" width="70px">
        </el-table-column>
        <el-table-column label="订单编号" width="180" align="center">
          <template slot-scope="scope">{{scope.row.Out_Trade_No}}</template>
        </el-table-column>
        <el-table-column label="商品名称" width="180" align="center">
          <template slot-scope="scope">{{scope.row.GoodsName}}...</template>
        </el-table-column>
        <el-table-column label="商品总数" width="180" align="center">
          <template slot-scope="scope">{{scope.row.DeliverCount}}</template>
        </el-table-column>
        <el-table-column label="提交时间" width="180" align="center">
          <template slot-scope="scope">{{scope.row.CreateTime | filteTime}}</template>
        </el-table-column>
           <el-table-column label="更新时间" width="180" align="center">
          <template slot-scope="scope">{{scope.row.UpdateTime | filteTime}}</template>
        </el-table-column>
        <!-- <el-table-column label="用户账号" align="center">
          <template slot-scope="scope">{{scope.row.UserName}}</template>
        </el-table-column> -->
       <el-table-column label="派单员" align="center">
          <template slot-scope="scope">{{scope.row.AdminName}}</template>
        </el-table-column>
        <el-table-column label="派单员联系电话" align="center">
          <template slot-scope="scope">{{scope.row.AdminTelPhone}}</template>
        </el-table-column>
        <!-- <el-table-column label="收货人电话" align="center">
          <template slot-scope="scope">{{scope.row.RcPhone}}</template>
        </el-table-column> -->
        <!-- <el-table-column label="订单金额" width="120" align="center">
          <template slot-scope="scope">￥{{scope.row.Money}}</template>
        </el-table-column> -->
        <el-table-column label="供货金额" width="120" align="center">
          <template slot-scope="scope">￥{{scope.row.SupplyMoney}}</template>
        </el-table-column>
        <el-table-column label="订单状态" width="120" align="center">
          <template slot-scope="scope">
            <div :style="{'color':scope.row.lineStatus==1?'red':''}">
            {{scope.row.lineStatus | lineStatusFilte}}
            </div>
            </template>
        </el-table-column>
        <el-table-column label="订单类型" align="center">
          <template slot-scope="scope">{{scope.row.OrderType |OrderFilte}}</template>
        </el-table-column>
        <el-table-column label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleViewOrder(scope.$index, scope.row)">查看订单</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--    <div class="batch-operate-container">
          <el-select size="small" v-model="operateType" placeholder="批量操作">
            <el-option
              v-for="item in operateOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button
            style="margin-left: 20px"
            class="search-button"
            @click="handleBatchOperate()"
            type="primary"
            size="small"
          >确定</el-button>
        </div>-->
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total,prev, pager, next,jumper"
        :current-page.sync="listQuery.opage"
        :page-size="listQuery.Rows"
        :total="total"
      ></el-pagination>
    </div>
    <!-- <el-dialog title="关闭订单" :visible.sync="closeOrder.dialogVisible" width="30%">
       <span style="vertical-align: top">操作备注：</span>
       <el-input
         style="width: 80%"
         type="textarea"
         :rows="5"
         placeholder="请输入内容"
         v-model="closeOrder.content"
       ></el-input>
       <span slot="footer" class="dialog-footer">
         <el-button @click="closeOrder.dialogVisible = false">取 消</el-button>
         <el-button type="primary" @click="handleCloseOrderConfirm">确 定</el-button>
       </span>
     </el-dialog>-->
    <logistics-dialog v-model="logisticsDialogVisible"></logistics-dialog>
  </div>
</template>
<script>
  import { fetchList, closeOrder, deleteOrder } from "@/api/order";
  import { formatDate } from "@/utils/date";
  import LogisticsDialog from "@/views/oms/order/components/logisticsDialog"; //订单跟踪
  import { mapActions } from "vuex";
  function  CreateTime(time){
    let date = new Date(time);
    return formatDate(date, "yyyy-MM-dd");
  }
  //当前时间加一天
  function formatDateAdd(needTime, N) { //当前时间加N天
    var CurrentDate = "";
    //needTime是整数，否则要parseInt转换
    var time = new Date(Date.parse(needTime) + 86400000 * N)
    var Year = time.getFullYear();
    var Month = time.getMonth() + 1;
    var Day = time.getDate();
    CurrentDate += Year + "-";
    if (Month >= 10) { CurrentDate += Month + "-"; }
    else { CurrentDate += "0" + Month + "-"; }
    if (Day >= 10) { CurrentDate += Day; }
    else { CurrentDate += "0" + Day; }
    return CurrentDate;
  }
  var LastWeek=new Date();
LastWeek.setDate(LastWeek.getDate()-7);
var SupplierID=JSON.parse(localStorage.getItem("userInfoS")).SuppliersID;
  const defaultListQuery = {
    opage:1,
    Offset: 0,//起始Index
    Rows: 10,
    KeyWords: null,
    BeginTime:null,
    EndTime: null,
    Status: 2,
    OrderNo: null,
    UserID:null,
    OrderType:null,
    SuppliersID:SupplierID,
    checkTime:[CreateTime(LastWeek), CreateTime(new Date())], //默认渲染时间为今天
  };
  export default {
    name: "order",
    components: { LogisticsDialog },
    data() {
      return {
        listQuery: Object.assign({}, defaultListQuery),
        listLoading: true,
        list: null,
        total:0,  //
        operateType: null,
        multipleSelection: [],
        closeOrder: {
          dialogVisible: false,
          content: null,
          orderIds: []
        },
       pickerOptions: {
        shortcuts: [
          {
            text: "昨天",
            onClick(picker) {
              //设置最近一周的时间v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一周",
            onClick(picker) {
              //设置最近一周的时间v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一月",
            onClick(picker) {
              //设置最近一月的时间v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
        statusOptions: [ //订单状态
          // {
          //   label: "未确认",
          //   value: 0
          // },
          {
            label: "待接单",
            value: 1
          },
          {
            label: "已接单",
            value: 2
          },
          {
            label: "已取消",
            value: 3
          },
          // {
          //   label: "已接单",
          //   value: 4
          // },
          // {
          //   label: "已取消",
          //   value:5
          // }
        ],
        operateOptions: [
          {
            label: "批量发货",
            value: 1
          },
          {
            label: "关闭订单",
            value: 2
          },
          {
            label: "删除订单",
            value: 3
          }
        ],
        logisticsDialogVisible: false,
        OrderTypeList:[
        {
          label: "订货单",
          value: 1
        },
        {
          label: "退货单",
          value: 2
        },
        {
          label: "加急单",
          value: 3
        }
      ]
      };
    },
    beforeRouteEnter(to, from, next) {
      // 路由导航钩子，此时还不能获取组件实例 `this`，所以无法在data中定义变量（利用vm除外）
      if(from.name=='orderDeliveryDetails'){
        to.meta.isBack=true;
        //判断是从哪个路由过来的，
        console.log("from"+from)
      }
      next();
      console.log("to.meta.isBack"+to.meta.isBack)
    },
    created() {
      // console.log(this.$store.dispatch("setOrderList"));

       let status=this.$route.query.status;
      if(status||status==0){
        this.listQuery.Status=Number(status);
      }
       this.getList();
    },
    filters: {
      OrderFilte(value){//订单类型
      if(value==1){
        return "订货单"
      }else if(value==2){
        return "退货单"
      }else if(value==3){
        return "加急单"
      }
    },
      filteTime(value){
      if(value.indexOf("T")>-1){
        return value.replace(/T/g,' ')
      }else{
        return value;
      }
    },
      formatCreateTime(time) {
        let date = new Date(time);
        return formatDate(date, "yyyy-MM-dd");
      },
      formatLineStatus(value) {
      //判断  当前订单行状态  (0.未派发；1.待接单；2.已接单；3.已取消；）
      if (value === 0) {
        return "未派发";
      } else if (value === 1) {
        return "待接单";
      } else if (value === 2) {
        return "已接单";
      } else {
        return "已取消";
      }
    },
      formatStatus(value) {
        if (value === 0) {
          return "未确认";
        } else if (value === 1) {
          return "已完成";
        } else if (value === 2) {
          return "已审核";
        } else if (value === 3) {
          return "待接单";
        } else if (value === 4) {
          return "已接单";
        } else if (value === 5) {
          return "已取消";
        }
      },
        lineStatusFilte(value){
        if(value==1){
          return"未处理"
        }else{
          return "已处理"
        }
      }
    },
    methods: {
      print(){//打印报表
      this.$PrintJS({
        printable: 'orderDeliveryID',//要打印内容的id
        type: 'html',
        style: 'td,th{text-align:center;}td:last-child{display:none}th:nth-last-child(2){display:none}td{border:1px solid #000}',
        scanStyles: false
      })
    },
      ...mapActions(["setOrderList"]),
      handleResetSearch() { //查询重置功能
        this.listQuery = Object.assign({}, defaultListQuery);
        this.listQuery.checkTime = null;
      },
      handleSearchList() { //查询搜索功能
        this.listQuery.opage=1;
        this.listQuery.Offset = 0; //起始数据为0条
        // console.log(this.listQuery);
        if(this.listQuery.checkTime ==null){  //必须选择时间
          this.listQuery.BeginTime ="";this.listQuery.EndTime ="";
          /*this.$alert('必选选择时间', '你好', {
            confirmButtonText: '确定',
          });*/
          this.$message({
            message: "必选选择时间",
            type: "warning",
            duration: 2000
          });

        }else{
          this.listQuery.BeginTime = CreateTime(this.listQuery.checkTime[0]);
          this.listQuery.EndTime =CreateTime(this.listQuery.checkTime[1]);
          this.getList();
        }
      },
      handleSelectionChange(val) { //表单内多选框操作
        this.multipleSelection = val;
      },
      handleViewOrder(index, row) { //查看订单详情   跳转到详情订单
        let path="/oms/orderDetail";
      if(row.OrderType==2){
          path="/oms/MRBdetail"
      }
        this.$router.push({ path:path, query: { OrderNo: row.Out_Trade_No,ct:  CreateTime(row.CreateTime)} });
      },
      handleSizeChange(val) { // 改变每页数量
        this.listQuery.opage = 1;
        this.listQuery.Offset = 0;
        this.listQuery.Rows = val;
        this.getList();
      },
      handleCurrentChange(val) { //翻页
        this.listQuery.opage = val;
        this.listQuery.Offset = this.listQuery.Rows*(this.listQuery.opage-1);   //Offset 从0开始计
        // console.log("Offset:"+this.listQuery.Offset)
        this.getList();
      },
      handleCloseOrderConfirm() {  //备注信息 关闭订单时才能写
        if (this.closeOrder.content == null || this.closeOrder.content === "") {
          this.$message({
            message: "操作备注不能为空",
            type: "warning",
            duration: 1000
          });
          return;
        }
        let params = new URLSearchParams();
        params.append("ids", this.closeOrder.orderIds);
        params.append("note", this.closeOrder.content);
        closeOrder(params).then(response => {
          this.closeOrder.orderIds = [];
          this.closeOrder.dialogVisible = false;
          this.getList();
          this.$message({
            message: "修改成功",
            type: "success",
            duration: 1000
          });
        });
      },
      getList() {//获取订单
        this.listLoading = true;
        /* this.listQuery.BeginTime =  CreateTime(this.listQuery.createTime[0]);
         this.listQuery.EndTime = CreateTime(this.listQuery.createTime[1]);
        */
        //查询的结束日期一定要加一条

        this.listQuery.BeginTime = CreateTime(this.listQuery.checkTime[0]);
        this.listQuery.EndTime = formatDateAdd(CreateTime(this.listQuery.checkTime[1]), 1)
        this.listQuery.Offset = this.listQuery.Rows*(this.listQuery.opage-1);   //Offset 从0开始计

        // console.log(this.listQuery.Offset)
        this.$request.GetListOrderforSuppier(this.listQuery).then((response) => {
          this.listLoading = false;
          if(!response.content){
             this.total = 0;
          }else{
            let content=response.content;
            content.forEach(item=>{
              // Status1: 3
              // Status2: 1
              // Status3: 3
              if(item.Status1>=2&&item.Status2>=2&&item.Status3>=2){
                    item.lineStatus=2;
              }else{
                 item.lineStatus=1;
              }
            })
            this.list =content;
            this.total = response.accattach;
          }

          // this.total = response.data.total;
        });
      },

      covertOrder(order) {
        let address =
          order.receiverProvince +
          order.receiverCity +
          order.receiverRegion +
          order.receiverDetailAddress;
        let listItem = {
          orderId: order.id,
          orderSn: order.orderSn,
          receiverName: order.receiverName,
          receiverPhone: order.receiverPhone,
          receiverPostCode: order.receiverPostCode,
          address: address,
          deliveryCompany: null,
          deliverySn: null
        };
        return listItem;
      }
    }
  };
</script>
<style scoped>
  .input-width {
    width: 203px;
  }
</style>





